import "./App.css";
import SceneWrapper from "./components/Helpers/SceneWrapper"
import { useEffect, createContext, useState } from "react"
import ReactGA from 'react-ga';

ReactGA.initialize('UA-216541182-1');

function App() {
  useEffect(() => {
    document.title = "Muhammed's Bookshelf Portfolio"
  }, []);
  useEffect( () => {

    // This line will trigger on a route change
    ReactGA.pageview(window.location.pathname + window.location.search); 

});

  return (
    <>


        <div className=" m-0 p-0  h-screen relative bg-wall bg-no-repeat bg-cover bg-center">
          <SceneWrapper />
        </div >

    </>
  );
}
export default App;
